import React from "react";
import styled from "styled-components";
import Assing from "../../images/assignment.png";
import Test from "../../images/quiz.png";
import Attendance from "../../images/attendance.png";
import Lab from "../../images/nanotechnology.png";
import { useMarksEntry } from "../../../Context/MarksEntry";

const AddAttendance = React.lazy(() => import("./Attendance/AddAttendance"));

const AddAssignment = React.lazy(() => import("./Assignment/AddAssignment"));
const AddQuizz = React.lazy(() => import("./Quizz/AddQuizz"));

const AddLabPerformance = React.lazy(() =>
  import("./LabPerform/AddLabPerformance")
);

const MarksActionContainer = () => {
  const marks = useMarksEntry();

  React.useEffect(() => {
    document.title = marks.title + "Bangladesh Open University";
  }, [marks]);

  return (
    <React.Fragment>
      {!marks.openAssign &&
        !marks.openAttendance &&
        !marks.openLab &&
        !marks.openQuizz && <MarkesGridButtonPanel />}
      {marks.openAttendance && <AddAttendance />}
      {marks.openAssign && marks.label !== null && <AddAssignment />}

      {marks.openQuizz && marks.label !== null && <AddQuizz />}
      {marks.openLab && <AddLabPerformance />}
    </React.Fragment>
  );
};

function MarkesGridButtonPanel() {
  const marks = useMarksEntry();

  document.onclick = (e) => {
    if (
      e.target.id === "assignParent" ||
      e.target.id === "assignParent2" ||
      e.target.id === "assignParent3"
    ) {
      document.getElementById("assign").style.display = "block";
    } else {
      let assign = document.getElementById("assign");
      if (assign !== null) {
        assign.style.display = "none";
      }
    }
    if (
      e.target.id === "quizzParent" ||
      e.target.id === "quizzParent2" ||
      e.target.id === "quizzParent3"
    ) {
      document.getElementById("quizz").style.display = "block";
    } else {
      let quizz = document.getElementById("quizz");
      if (quizz !== null) {
        quizz.style.display = "none";
      }
    }
  };

  return (
    <ActionPanel>
      <Title>Choose Option</Title>
      <ButtonPanelGrid>
        <GridButton id="assignParent">
          <Image id="assignParent2" src={Assing} alt="Import File" />
          <Text id="assignParent3">Assignment</Text>
          <Option id="assign">
            <Ul style={{ padding: 0 }}>
              <Li
                onClick={() => {
                  marks.handleBackToOption(true);
                  marks.handleSetLabel(1);
                  marks.handleOpenAssignment(true);
                  marks.handleSetTitle("Add Marks - Assignment 1");
                }}
              >
                Assignment-1
              </Li>
              <Li
                onClick={() => {
                  marks.handleBackToOption(true);
                  marks.handleSetLabel(2);
                  marks.handleOpenAssignment(true);
                  marks.handleSetTitle("Add Marks - Assignment 2");
                }}
              >
                Assignment-2
              </Li>
              <Li
                onClick={() => {
                  marks.handleBackToOption(true);
                  marks.handleSetLabel(3);
                  marks.handleOpenAssignment(true);
                  marks.handleSetTitle("Add Marks - Assignment 2");
                }}
              >
                Assignment-3
              </Li>
            </Ul>
          </Option>
        </GridButton>
        <GridButton id="quizzParent">
          <Image id="quizzParent2" src={Test} alt="Import File" />
          <Text id="quizzParent3">Quizzes</Text>
          <Option id="quizz">
            <Ul style={{ padding: 0 }}>
              <Li
                onClick={() => {
                  marks.handleBackToOption(true);
                  marks.handleSetLabel(1);
                  marks.handleOpenQuizz(true);
                  marks.handleSetTitle("Add Marks - Quizz 1");
                }}
              >
                Quizz-1
              </Li>
              <Li
                onClick={() => {
                  marks.handleBackToOption(true);
                  marks.handleSetLabel(2);
                  marks.handleOpenQuizz(true);
                  marks.handleSetTitle("Add Marks - Quizz 2");
                }}
              >
                Quizz-2
              </Li>
            </Ul>
          </Option>
        </GridButton>
        {marks.classInfo.courseType === "Lab" && (
          <GridButton
            onClick={() => {
              marks.handleBackToOption(true);
              marks.handleSetLabel(null);
              marks.handleOpenLab(true);
              marks.handleSetTitle("Add Marks - Lab Performance");
            }}
          >
            <Image src={Lab} alt="Import File" />
            <Text>Lab Performance</Text>
          </GridButton>
        )}
        <GridButton
          onClick={() => {
            marks.handleBackToOption(true);
            marks.handleSetLabel(null);
            marks.handleOpenAttendance(true);
            marks.handleSetTitle("Add Marks - Attendance");
          }}
        >
          <Image src={Attendance} alt="Sample File" />
          <Text>Attendance</Text>
        </GridButton>
      </ButtonPanelGrid>
    </ActionPanel>
  );
}

export default MarksActionContainer;

const ActionPanel = styled.div`
  width: 550px;
  border-radius: 5px;
  padding: 1rem;
  display: flex;
  flex-direction: column;
  align-items: center;

  @media (max-width: 560px) {
    width: 95%;
  }
`;
const ButtonPanelGrid = styled.div`
  display: flex;
  align-items: stretch;
  justify-items: stretch;
  padding: 1rem;
  flex-wrap: wrap;
  justify-content: center;
`;
const GridButton = styled.div`
  width: 130px;
  height: 150px;
  cursor: pointer;
  margin: 1rem;
  overflow: hidden;
  border: 1px solid rgba(3, 3, 3, 0.3);
  border-radius: 5px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: relative;
`;

const Image = styled.img`
  width: 40%;
`;

const Title = styled.h1`
  font-size: 1.2rem;
  font-weight: 500;
  text-align: center;
`;

const Text = styled.p`
  text-align: center;
  margin-bottom: 0;
  margin-top: 5px;
  font-size: 0.9rem;
`;

const Option = styled.div`
  width: 130px;
  padding: 0.5rem 0.1rem;
  border-radius: 3px;
  position: absolute;
  background: #fff;
  box-shadow: 1px 1px 1px 1px rgba(3, 3, 3, 1);
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: none;
`;

const Li = styled.li`
  font-size: 0.9rem;
  padding: 0.5rem 0.2rem;
  list-style-type: none;
  font-weight: 500;
  cursor: pointer;
  text-align: center;
  width: 100%;
  border-radius: 5px;
  &:hover {
    background: rgba(199, 236, 238, 0.5);
  }
`;

// const Button = styled.button`
//   position: absolute;
//   top: 0;
//   left: 0;
//   right: 0;
//   bottom: 0;
//   background: transparent;
// `;

const Ul = styled.ul`
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
`;
